<script>
  import { elasticOut } from "svelte/easing";

  // Contains an array of Javascript objects.
  // Each object contains: 
  // [
  //    {
  //         className: string
  //         image: string (url to image)
  //         members: [{
  //           name: string
  //           major: string
  //           pixelLocationX: number (pixel location on class picture)
  //           pixelLocationY: number (same but on y axis)
  //           linkedIn: string (the url)
  //         },
  //     ...]
  //    },
  // ...]
  // basically an ar
  // order from newest to oldest
  export let pledgeClasses;

  let visible = true;

  let classIndex = 0;
  let curClass = pledgeClasses[classIndex];

  let memberIndex = 0;
  let curMember = curClass.members[memberIndex];

  function zoom(node, params) {
    console.log(getComputedStyle(node));
    const existingTransform = 
        getComputedStyle(node).transform.replace('none', '');
    
    return {
			delay: params.delay || 0,
			duration: params.duration || 400,
			easing: params.easing || elasticOut,
			css: (t, u) => `transform: ${existingTransform} scale(${t})`
		};
  }

  function toggleVisible() {
    visible= False;
    visible = True;
  }
</script>

<style>
/* Sorry for the many divs; its the only way I can layer it so it looks good. */
  /* .pledge-classes {
    width: 70vw;
    max-width: calc(100vw - 10em);
    height: 40vw;
    border: 7px black solid;
    background-color: white;
    padding: 2em;
  } */

  .class-img-container {
    position: relative;
    width: 70vw;
    max-width: calc(100vw - 10em);
    height: 70vh;
    overflow: hidden;
  }

  .class-img {
    position: absolute;
    top: -9999px;
    left: -9999px;
    right: -9999px;
    bottom: -9999px;
    height: 100%;
    margin: auto;
    z-index: 1;
    transform: scale(1.7);
    transform-origin: 50% 75%;
  }
</style>

<button on:click="{toggleVisible}">reset</button>
{#if visible}
  <div class="pledge-classes">
    <div class="class-img-container">
      <img
        on:click="{zoom}"
        class="class-img"
        src="{curClass.image}"
        alt="{curClass.className}"
      >
    </div>
  </div>
{/if}
